






















import Vue from 'vue'

interface education {
    image: string,
    title: string,
    solution: string,
    website: string,
    sDate: string,
    eDate: string,
    total: string
}

export default Vue.extend({
    data() {
        return {
            title: 'Education' as string,
            educations: [
                {
                    image: require(`../../assets/images/SBS.jpg`),
                    title: 'SBS아카데미컴퓨터아트학원',
                    solution: '국비과정 스마트기기UXUI디자인양성과정',
                    website: 'https://se.sbsart.com/',
                    sDate: '2021-07-12',
                    eDate: '2021-11-24',
                    total: '',
                },
                {
                    image: require(`../../assets/images/KIT.jpg`),
                    title: '경남정보대학교',
                    solution: 'ICT컴퓨터계열 IoT전공',
                    website: 'https://www.kit.ac.kr/',
                    sDate: '2017-02-23',
                    eDate: '2021-02-18',
                    total: '',
                },
                {
                    image: require(`../../assets/images/sejeong.jpg`),
                    title: '세정고등학교',
                    solution: '상업고 스마트경영과',
                    website: 'www.sejeong.hs.kr/',
                    sDate: '2014-03-03',
                    eDate: '2017-02-14',
                    total: '',
                }
            ] as education[],
        }
    },
    created() {
        this.totalDateCalculation();
    },
    methods: {
        totalDateCalculation() {
            const educations: education[] = this.educations;

            for (let education of educations) {
                if (education.eDate) {
                    education.total = '졸업';
                } else {
                    education.total = '재학중';
                }
            } // For End  
        }
    },
})
